import React, { useContext, useEffect, useState } from "react";
import { ProgressBar } from "react-bootstrap";
import { BotsContext, DealsContext, ErrorsContext } from "../../../App";
import { fetchBots } from "../../../state/bots/BotsEffects";
import { fetchDealsForBotFor30Days } from "../../../state/deals/DealsEffects";
import { isBots30daysDealsLoaded } from "../../../state/deals/DealsSelectors";
import { getDealsLimiter, setDealsLimiter } from "../../../utils/ApiUtils";
import "./BotsListCharts.css";
import { Bar, Line } from "react-chartjs-2";
import {
  AccumulativeChartOptions,
  MaxUsageManyBotsChartOptions,
  ProfitChartOptions,
} from "../../../utils/ChartsUtils";
import {
  calculateProfitPerDayForListOfBots,
  getAccumulativeProfit30DaysChartData,
  getProfit30DaysChartData,
} from "../../../utils/DealsUtils";
import { getBotsLeaderboardChartData } from "../../../utils/BotsUtils";

const BotsListCharts = () => {
  const { botsState, botsDispatch } = useContext(BotsContext);
  const { dealsState, dealsDispatch } = useContext(DealsContext);
  const { deals } = dealsState;
  const { list, hasMore, offset, bots } = botsState;
  const { errorsDispatch } = useContext(ErrorsContext);
  const [dealsLoaded, setDealsLoaded] = useState(false);
  const [botsLoaded, setBotsLoaded] = useState(false);

  const [numberHandled, setNumberHandled] = useState(0);
  const [profitsChartData, setProfitsChartData] = useState<any>([[], []]);
  const [accumulativeProfitsChartData, setAccumulativeProfitsChartData] =
    useState<any>([[], []]);
  const [botsLeaderboardChartData, setBotsLeaderboardChartData] = useState<any>(
    [[], [], []]
  );

  //first get all bots
  const getBotsList = async () => {
    if (hasMore && offset !== 0) {
      await fetchBots(botsDispatch, botsState, errorsDispatch, false, 100);
    }
  };

  useEffect(() => {
    getBotsList().then(() => {
      if (!hasMore) {
        setBotsLoaded(true);
      }
    });
  }, [offset]);

  // get deals
  const getBoatsDeals = async () => {
    setDealsLimiter(Math.random());

    for (const botId of list) {
      if (!isBots30daysDealsLoaded(dealsState, botId)) {
        await fetchDealsForBotFor30Days(
          dealsState,
          dealsDispatch,
          errorsDispatch,
          botId
        );
      }
    }
  };

  useEffect(() => {
    if (botsLoaded) {
      getBoatsDeals().then(() => {
        setDealsLoaded(true);
      });
    }
  }, [botsLoaded]);

  // then calculate data for charts
  useEffect(() => {
    if (dealsLoaded) {
      const profitsObject = calculateProfitPerDayForListOfBots(
        dealsState,
        list
      );
      setProfitsChartData(getProfit30DaysChartData(profitsObject));
      setAccumulativeProfitsChartData(
        getAccumulativeProfit30DaysChartData(profitsObject)
      );
      setBotsLeaderboardChartData(getBotsLeaderboardChartData(botsState));
    }
  }, [dealsLoaded, bots]);

  // reset limiter everytime to stop all the jobs
  useEffect(() => {
    return () => {
      const dealsLimiter = getDealsLimiter();
      dealsLimiter
        .stop({ dropWaitingJobs: true })
        .then(() => dealsLimiter.disconnect());
    };
  }, []);

  // update data for loader
  useEffect(() => {
    const botsWithLoadedDeals = list.filter((botId: number) =>
      isBots30daysDealsLoaded(dealsState, botId)
    );
    setNumberHandled((botsWithLoadedDeals.length / list.length) * 100);
  }, [deals]);

  return (
    <div className="BotsListCharts">
      {!dealsLoaded && (
        <div className="ProgressBar">
          {!botsLoaded ? (
            <ProgressBar animated now={100} />
          ) : (
            <ProgressBar
              now={Math.ceil(numberHandled)}
              label={`${Math.ceil(numberHandled)}%`}
            />
          )}
        </div>
      )}
      {dealsLoaded && (
        <>
          <div className="ChartContainer">
            <Line
              options={AccumulativeChartOptions as any}
              data={{
                labels: accumulativeProfitsChartData[0],
                datasets: [
                  {
                    label: "Accumulative Profit, $",
                    data: accumulativeProfitsChartData[1],
                    borderColor: "rgb(53, 162, 235)",
                    backgroundColor: "rgba(53, 162, 235, 0.5)",
                    fill: { value: 0 },
                  },
                ],
              }}
            />
          </div>
          <div className="ChartContainer">
            <Bar
              options={ProfitChartOptions as any}
              data={{
                labels: profitsChartData[0],
                datasets: [
                  {
                    label: "Profit, $",
                    data: profitsChartData[1],
                    borderColor: "rgb(53, 162, 235)",
                    backgroundColor: "rgba(53, 162, 235, 0.5)",
                  },
                ],
              }}
            />
          </div>
          <div className="ChartContainer">
            <Bar
              options={
                MaxUsageManyBotsChartOptions(botsLeaderboardChartData[0]) as any
              }
              data={{
                labels: botsLeaderboardChartData[0],
                datasets: [
                  {
                    label: "Funds locked, $",
                    data: botsLeaderboardChartData[1],
                    borderColor: "rgb(255, 99, 132)",
                    backgroundColor: "rgba(255, 99, 132, 0.8)",
                  },
                  {
                    label: "Max usage, $",
                    data: botsLeaderboardChartData[2],
                    borderColor: "rgb(53, 162, 235)",
                    backgroundColor: "rgba(53, 162, 235, 0.5)",
                  },
                ],
              }}
            />
          </div>
        </>
      )}
    </div>
  );
};

export default BotsListCharts;
