import { getAllDealsWithin30DaysForOneBot } from "../../utils/DealsUtils";
import { getDealsListForBotFor30Days } from "./DealsActions";
import { IDealsState } from "./DealsReducer";

export async function fetchDealsForBotFor30Days(
  dealsState: IDealsState,
  dealsDispatch: any,
  errorsDispatch: any,
  botId: number
) {
  const { deals } = dealsState;
  try {
    if (botId in deals && "per30d" in deals[botId]) {
      return;
    }
    const data = await getAllDealsWithin30DaysForOneBot(botId);
    dealsDispatch(getDealsListForBotFor30Days(data, botId));
  } catch (e: any) {
    if (!e.message.includes("This limiter has been stopped")) {
      errorsDispatch({
        type: "addError",
        payload: `Error when fetching deals for bot ${botId}`,
      });
    }
  }
}
