import { IBot, IBotFilters } from "../../interfaces/Bot";

interface ISetNewBotsList {
  type: "setNewBotsList";
  payload: Array<IBot>;
}

export const setNewBotsList = (botsList: Array<IBot>): ISetNewBotsList => ({
  type: "setNewBotsList",
  payload: botsList,
});

interface ISetHasMore {
  type: "setHasMore";
  payload: boolean;
}

export const setHasMore = (hasMore: boolean): ISetHasMore => ({
  type: "setHasMore",
  payload: hasMore,
});

interface IAddToBotsList {
  type: "addToBotsList";
  payload: Array<IBot>;
}

export const addToBotsList = (botsList: Array<IBot>): IAddToBotsList => ({
  type: "addToBotsList",
  payload: botsList,
});

interface ISetBotsOffset {
  type: "setBotsOffset";
  payload: number;
}

export const setBotsOffset = (offset: number): ISetBotsOffset => ({
  type: "setBotsOffset",
  payload: offset,
});

interface ISetBotsFilters {
  type: "setBotsFilters";
  payload: IBotFilters;
}

export const setBotsFilters = (botsFilter: IBotFilters): ISetBotsFilters => ({
  type: "setBotsFilters",
  payload: botsFilter,
});

interface ISetBot {
  type: "setBot";
  payload: { botId: number; bot: IBot };
}

export const setBot = (botId: number, bot: IBot): ISetBot => ({
  type: "setBot",
  payload: { botId, bot },
});

export type BotsAction =
  | ISetNewBotsList
  | ISetHasMore
  | IAddToBotsList
  | ISetBotsOffset
  | ISetBotsFilters
  | ISetBot;
