import { IDeal } from "../../interfaces/Deal";

interface IGetDealsListForBotFor30Days {
  type: "getDealsListForBotFor30Days";
  payload: { deals: Array<IDeal>; botId: number };
}

export const getDealsListForBotFor30Days = (
  deals: Array<IDeal>,
  botId: number
): IGetDealsListForBotFor30Days => ({
  type: "getDealsListForBotFor30Days",
  payload: { deals, botId },
});

export type DealsAction = IGetDealsListForBotFor30Days;
