import { getBots, getBotStats } from "../../utils/ApiUtils";
import { getMaxUsage, getReturns } from "../../utils/BotsUtils";
import { IDealsState } from "../deals/DealsReducer";
import {
  addToBotsList,
  setBotsOffset,
  setHasMore,
  setNewBotsList,
  setBot,
} from "./BotsActions";
import { IBotsState } from "./BotsReducer";

export async function fetchBots(
  botsDispatch: any,
  botsState: IBotsState,
  errorsDispatch: any,
  resetOffset: boolean = false,
  customLimit?: number
) {
  botsDispatch(setHasMore(true));
  const {
    offset,
    limit,
    filters: { sorting, scope, type, account },
  } = botsState;

  const offsetLimit = customLimit ? customLimit : limit;
  try {
    const data = await getBots(
      offset,
      offsetLimit,
      sorting,
      scope,
      type,
      account,
      resetOffset
    );
    if (resetOffset) {
      botsDispatch(setBotsOffset(offsetLimit));
      botsDispatch(setNewBotsList(data));
    } else {
      botsDispatch(setBotsOffset(offset + offsetLimit));
      botsDispatch(addToBotsList(data));
    }
    if (data.length < offsetLimit) {
      botsDispatch(setHasMore(false));
    }
  } catch {
    botsDispatch(setNewBotsList([]));
    botsDispatch(setBotsOffset(offsetLimit));
    botsDispatch(setHasMore(false));
    errorsDispatch({
      type: "addError",
      payload: "Error when fetching bots list",
    });
  }
}

export async function getMaxUsageEffect(
  botsDispatch: any,
  botsState: IBotsState,
  errorsDispatch: any,
  botId: number,
  account: number
) {
  const { bots } = botsState;
  const botItem = bots[botId];
  try {
    if (!("maxUsage" in bots[botId])) {
      const maxUsage = await getMaxUsage(botItem, account);
      const bot = { ...botItem, maxUsage };
      botsDispatch(setBot(botItem.id, bot));
    }
  } catch {
    const bot = { ...botItem, maxUsage: 0 };
    botsDispatch(setBot(botItem.id, bot));
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching max usage for bot ${botItem.name}`,
    });
  }
}

export async function getFundsLockedAndTodaysProfit(
  botsDispatch: any,
  botsState: IBotsState,
  errorsDispatch: any,
  botId: number
) {
  const { bots } = botsState;
  const botItem = bots[botId];
  try {
    if (!("fundsLocked" in bots[botId])) {
      const {
        profits_in_usd: { today_usd_profit, funds_locked_in_active_deals },
      } = await getBotStats(botId);

      const bot = {
        ...botItem,
        fundsLocked: funds_locked_in_active_deals,
        todayUsdProfit: today_usd_profit,
      };
      botsDispatch(setBot(botItem.id, bot));
    }
  } catch {
    const bot = {
      ...botItem,
      fundsLocked: 0,
      todayUsdProfit: 0,
    };
    botsDispatch(setBot(botItem.id, bot));
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching stats for bot ${botItem.name}`,
    });
  }
}

export async function getReturnsEffect(
  botsDispatch: any,
  botsState: IBotsState,
  dealsState: IDealsState,
  errorsDispatch: any,
  botId: number
) {
  const { bots } = botsState;
  const botItem = bots[botId];
  try {
    const deals30daysForBot = dealsState.deals[botId].per30d;
    const maxUsage = botItem.maxUsage;
    const bot = {
      ...botItem,
      returns: getReturns(maxUsage, deals30daysForBot),
    };
    botsDispatch(setBot(botItem.id, bot));
  } catch {
    const bot = { ...botItem, returns: 0 };
    botsDispatch(setBot(botItem.id, bot));
    errorsDispatch({
      type: "addError",
      payload: `Error when getting returns for bot ${botItem.name}`,
    });
  }
}
