import CryptoJS from "crypto-js";
import Bottleneck from "bottleneck";
import { IBotApi } from "../interfaces/Bot";
import { IDealApi } from "../interfaces/Deal";

const commonLimiter = new Bottleneck({
  reservoir: 13,
  reservoirRefreshAmount: 13,
  reservoirRefreshInterval: 1000,
});

export const DEAL_LIMITER_SETTINGS = {
  id: "DEAL_LIMITER",
  reservoir: 0,
  reservoirRefreshAmount: 1,
  reservoirRefreshInterval: 1000,
  minTime: 1000,
  maxConcurrent: 1,
  clearDatastore: true,
  timeout: 2000,
};

//we need that hack for stopping loading heavy deals request
export let dealsLimiter = new Bottleneck(DEAL_LIMITER_SETTINGS);

export function setDealsLimiter(limiterId: any) {
  dealsLimiter = new Bottleneck({ ...DEAL_LIMITER_SETTINGS, id: limiterId });
}

export function getDealsLimiter() {
  return dealsLimiter;
}

export function getSig(path: string, secret: string) {
  return CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, secret)
    .update(path)
    .finalize()
    .toString(CryptoJS.enc.Hex);
}
export const callApi = (
  method: string,
  path: string,
  limiter: any = commonLimiter
) => {
  const API_KEY =
    new URLSearchParams(window.location.search).get("api_key") || "";
  const SECRET =
    new URLSearchParams(window.location.search).get("secret") || "";

  return limiter.schedule(() =>
    fetch(`https://api.3commas.io${path}`, {
      method: method,
      headers: {
        APIKEY: API_KEY,
        Signature: getSig(path, SECRET),
      },
    })
  );
};

export const getBots = async (
  offset: number,
  limit: number,
  sorting: string,
  scope: string,
  type: string,
  accountId: string,
  resetOffset = false
): Promise<Array<IBotApi>> => {
  const [sortBy, sortDirection] = sorting.split("-");

  const getBotsUrl = () =>
    `/public/api/ver1/bots?offset=${
      resetOffset ? 0 : offset
    }&limit=${limit}&sort_by=${sortBy}&sort_direction=${sortDirection}${
      scope ? `&scope=${scope}` : ""
    }${type ? `&strategy=${type}` : ""}${
      accountId ? `&account_id=${accountId}` : ""
    }`;

  return callApi("GET", getBotsUrl()).then((response: any) => response.json());
};

export const getAccounts = async () => {
  return callApi("GET", `/public/api/ver1/accounts`).then((response: any) =>
    response.json()
  );
};

export const getDeals = async (
  botId: number,
  offset: number,
  limit: number,
  scope: string,
  order: string
): Promise<Array<IDealApi>> => {
  return callApi(
    "GET",
    `/public/api/ver1/deals?bot_id=${botId}&offset=${offset}&limit=${limit}&scope=${scope}&order=${order}`,
    dealsLimiter
  ).then((response: any) => response.json());
};

export const getStrategies = async () => {
  return await callApi(
    "GET",
    `/public/api/ver1/marketplace/items?&limit=1000`
  ).then((response: any) => response.json());
};

export const getBotStats = async (botId: number) => {
  return await callApi(
    "GET",
    `/public/api/ver1/bots/stats?bot_id=${botId}`
  ).then((response: any) => response.json());
};
