import React, { useContext, useEffect, useState } from "react";
import { BotPreview } from "./BotPreview/BotPreview";
import InfiniteScroll from "react-infinite-scroll-component";
import "./BotsList.css";
import { AccountsContext, ErrorsContext, BotsContext } from "../../App";
import { Button, Spinner } from "react-bootstrap";
import { fetchBots } from "../../state/bots/BotsEffects";
import BotsListCharts from "./BotsListCharts/BotsListCharts";

export const BotsList = () => {
  const { botsState, botsDispatch } = useContext(BotsContext);
  const { list, hasMore, filters, bots } = botsState;
  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);
  const { errorsDispatch } = useContext(ErrorsContext);

  const [showCharts, setShowCharts] = useState(false);

  const getAccountByBotId = (accountId: any) =>
    accounts.find((account: any) => account.id === accountId);

  const isNothingFound = !hasMore && list.length === 0;

  useEffect(() => {
    setShowCharts(false);
  }, [filters]);

  return isNothingFound ? (
    <div className="FallbackContainer">
      <p style={{ textAlign: "center" }}>
        <b>No results found =(</b>
      </p>
    </div>
  ) : (
    <InfiniteScroll
      dataLength={list.length}
      next={async () => {
        await fetchBots(botsDispatch, botsState, errorsDispatch, false);
      }}
      hasMore={hasMore}
      loader={
        <div className="SpinnerContainer">
          <Spinner animation="grow" />
        </div>
      }
      endMessage={
        <p style={{ textAlign: "center" }}>
          <b>Yay! You have seen it all</b>
        </p>
      }
    >
      {showCharts ? (
        <BotsListCharts />
      ) : (
        <div className="ShowChartsButtonContainer">
          <Button
            className="ShowChartsButton"
            onClick={() => {
              setShowCharts(true);
            }}
          >
            Click to load charts
          </Button>
          <div className="ShowChartsDisclamer">
            Be careful, it can take a while if there are many bots in the list!
          </div>
        </div>
      )}
      <div className="BotsList">
        {accounts.length > 0 &&
          list.map((botId: number) => (
            <BotPreview
              botItem={bots[botId]}
              key={botId}
              account={getAccountByBotId(bots[botId].account_id)}
            />
          ))}
      </div>
    </InfiniteScroll>
  );
};
