export const MaxUsageManyBotsChartOptions = (labels: any) => ({
  indexAxis: "y" as const,
  scales: {
    x: {
      stacked: false,
      beginAtZero: true,
    },
    y: {
      stacked: true,
      beginAtZero: true,
      ticks: {
        callback: (value: any) => {
          return labels[value].length > 35
            ? labels[value].slice(0, 33) + "..."
            : labels[value];
        },
      },
    },
  },
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "Funds locked/Max usage, $",
    },
    tooltip: {
      intersect: false,
      mode: "y",
    },
  },
});
