import { getDeals } from "./ApiUtils";
import moment from "moment";
import { IDeal } from "../interfaces/Deal";
import _ from "lodash";
import { IDealsState } from "../state/deals/DealsReducer";

export const differenceInHours = (a: any, b: any) =>
  Math.floor((a.getTime() - b.getTime()) / (1000 * 60 * 60));

const HOURS_IN_30_DAYS = 24 * 30;

const getDealAgeInHours = (deal: any) =>
  differenceInHours(new Date(), new Date(deal.closed_at));

export const getAllDealsWithin30DaysForOneBot = async (botId: number) => {
  const limit = 1000;
  let offset = 0;
  let deals: any = [];

  const fetchAllDealsWithin30DaysInLoop = async () => {
    const newDeals = await getDeals(
      botId,
      offset,
      limit,
      "finished",
      "closed_at"
    );

    const [lastDeal] = newDeals.slice(-1);

    if (
      newDeals.length === limit &&
      getDealAgeInHours(lastDeal) <= HOURS_IN_30_DAYS
    ) {
      const filteredDeals = newDeals.filter(
        (deal: any) => getDealAgeInHours(deal) <= HOURS_IN_30_DAYS
      );
      offset = offset + limit;
      deals = [...deals, ...filteredDeals];
      await fetchAllDealsWithin30DaysInLoop();
    } else {
      deals = [...deals, ...newDeals];
    }
  };

  await fetchAllDealsWithin30DaysInLoop();

  return deals;
};

export const getObjectOfProfitPerDay = (deals: Array<IDeal>) => {
  const dateString = (item: IDeal) =>
    moment(item.closed_at).format("YYYY-MM-DD");

  let result = _.groupBy(deals, dateString);
  return _.mapValues(result, (deals: Array<IDeal>) =>
    _.sum(deals.map((deal: IDeal) => parseFloat(deal.usd_final_profit)))
  );
};

export const calculateProfitPerDayForListOfBots = (
  dealsState: IDealsState,
  botsIds: Array<number>
) => {
  return botsIds.reduce((currentObj: any, botId: number) => {
    const botsDeals = getObjectOfProfitPerDay(dealsState.deals[botId].per30d);

    return _.mergeWith(
      botsDeals,
      currentObj,
      (profit1: number, profit2: number) => {
        profit1 = profit1 || 0;
        profit2 = profit2 || 0;
        return profit1 + profit2;
      }
    );
  }, {});
};

const getLastThirtyDays = () =>
  [...new Array(30)]
    .map((i, idx) => moment().startOf("day").subtract(idx, "days"))
    .map((date: any) => date.format("YYYY-MM-DD"))
    .reverse();

export const getProfit30DaysChartData = (profitObject: any) => {
  const lastThirtyDays = getLastThirtyDays();

  const profits = lastThirtyDays.map((day: string) => {
    return day in profitObject ? profitObject[day] : 0;
  });

  const labels = lastThirtyDays.map((day: any) => moment(day, "YYYY-MM-DD"));
  return [labels, profits];
};

export const getAccumulativeProfit30DaysChartData = (profitObject: any) => {
  const profits = getProfit30DaysChartData(profitObject);
  let currentvalue = 0;
  const accProfits = profits[1].map((profit: any) => {
    const acc = currentvalue + profit;
    currentvalue = acc;
    return acc;
  });
  return [profits[0], accProfits];
};
