import _ from "lodash";
import { IBot, IBotFilters } from "../../interfaces/Bot";
import { BotsAction } from "./BotsActions";

export interface IBotsState {
  list: Array<number>;
  hasMore: boolean;
  offset: number;
  filters: IBotFilters;
  limit: number;
  bots: { [key: number]: IBot };
}

export const botsStateInitializer = (
  initialValue = initialBotsState
): IBotsState => {
  let storedFilters = null;
  try {
    storedFilters = JSON.parse(
      (localStorage as any).getItem("botsFiltersState")
    );
  } catch {}
  if (storedFilters) {
    return { ...initialValue, filters: storedFilters };
  } else {
    return initialValue;
  }
};

const initialFiltersState = {
  scope: "enabled",
  type: "",
  account: "",
  sorting: "created_at-desc",
};

export const initialBotsState: any = {
  list: [],
  hasMore: true,
  limit: 10,
  offset: 0,
  filters: initialFiltersState,
  bots: {},
};

export function botsReducer(
  state: IBotsState = initialBotsState,
  action: BotsAction
): IBotsState {
  switch (action.type) {
    case "setNewBotsList":
      const newList = action.payload.map((bot) => bot.id);
      const newBots = _.keyBy(action.payload, "id");
      return { ...state, list: newList, bots: newBots };
    case "setHasMore":
      return { ...state, hasMore: action.payload };
    case "addToBotsList":
      const list = action.payload.map((bot) => bot.id);
      const bots = _.keyBy(action.payload, "id");
      return {
        ...state,
        list: [...state.list, ...list],
        bots: { ...state.bots, ...bots },
      };
    case "setBotsOffset":
      return { ...state, offset: action.payload };
    case "setBotsFilters":
      return { ...state, filters: action.payload };
    case "setBot":
      return {
        ...state,
        bots: { ...state.bots, [action.payload.botId]: action.payload.bot },
      };
    default:
      throw new Error();
  }
}
